body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --main-toolbars-bgcolor:#397175;
  --main-secondary-color: #6A685B;
  --main-bg-color: #AADDCF;
  --main-title-text-color: #f2f2f2;
  --main-text-color: #333
}

.App{
  font-family: 'Orbitron', sans-serif;
}

h4 {
  margin-bottom:0px
}
.editor__container {
    width:800px;
    margin:20px auto;
    transition: width 0.5s, min-height 0.5s;
    -webkit-transition: width 0.5s, min-height 0.5s;
    -moz-transition: width 0.5s, min-height 0.5s;
    -ms-transition: width 0.5s, min-height 0.5s;
    -o-transition: width 0.5s, min-height 0.5s;
}

.editor__container.maximized {
    width:95vw;
    min-height: 100vh;
    margin:20px auto

}


.editor__textarea{
    font-family: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
    width:99%;
    box-shadow: 1px 4px 10px 1px #333;
    padding:5px;
    min-height:200px;
    color: var(--main-text-color);
    border:1px solid var(--main-secondary-color);
    border-top:none;
    transition:none;
    -webkit-transition:none;
    -moz-transition:none;
    -ms-transition:none;
    -o-transition:none;
}


.editor__textarea:focus{
    outline:none
}

.editor__container.maximized .editor__textarea{
    min-height: 80vh;
}
.toolbar {
    width:99%;
    padding: 5px;
    border:1px solid var(--main-secondary-color);
    color: var(--main-title-text-color);
    position: relative;
    background-color: var(--main-toolbars-bgcolor);
  }
  
  .fullScreen__icon {
    position: absolute;
    right: 5px;
    top: 10px;
    cursor:pointer;
  }
  
  .fullScreen__icon:hover {
    opacity:0.8;
  }
.prev__container{
    width:1000px;
    margin:20px auto;
    min-height: 200px;
    transition: width 0.5s, min-height 0.5s;
    -webkit-transition: width 0.5s, min-height 0.5s;
    -moz-transition: width 0.5s, min-height 0.5s;
    -ms-transition: width 0.5s, min-height 0.5s;
    -o-transition: width 0.5s, min-height 0.5s;
}

.prev__container.maximized {
    width:95vw;
    min-height: 100vh;
    margin:20px auto

}

.prev__screen{
    font-family: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
    background-color: #cfcfd1f6;
    color:#333;
    min-height: 100px;
    padding:5px;
    width:99%;;
    box-shadow: 1px 4px 10px 1px #333;
    border:1px solid var(--main-secondary-color);
    border-top:none
}

.prev__screen h1, .prev__screen h2{
    border-bottom:1px solid #333;
}

.prev__screen pre{
    background-color: #fff;
}

.prev__screen code{
    background-color: #fff;
}
